<template>
  <div class="top-bar" 
    @mousedown="mousedown"
  >

  </div>
</template>

<script>
export default {
  data() {
    return {
      /** 鼠标已经按下 */
      isMousedown: false,
      /** 鼠标移动过程中 */
      isMousemove: false,
      /** 开始移动的时候鼠标的位置 */
      startMousePoint: {
        x: '',
        y: ''
      },
      /** 开始移动的时候Window的位置 */
      startWindowPoint: {
        x: '',
        y: ''
      },
      /** 初始相对位置 */
      absolutePoint: {
        x: '',
        y: ''
      }
    }
  },
  methods: {
    /** 鼠标按下 */
    mousedown(e) {
      this.isMousedown = true
      this.startMousePoint = this.getMousePoint(e)
      this.startWindowPoint = this.getWindowPoint(e)
      this.absolutePoint.x = e.layerX
      this.absolutePoint.y = e.layerY

      const mousemoveHandler = (e) => {
        if(this.isMousedown) {
          // 获取鼠标当前位置
          const currentMousePoint = this.getMousePoint(e)
          // 求出位置差
          const difference = {
            x: currentMousePoint.x - this.startMousePoint.x,
            y: currentMousePoint.y - this.startMousePoint.y
          }

          // 获取窗口当前坐标
          const currentWindowPoint = this.getWindowPoint(e)

          // 换算VH单位
          const VH = window.innerHeight/100
          currentWindowPoint.x = currentWindowPoint.x/VH
          currentWindowPoint.y = currentWindowPoint.y/VH

          if(currentWindowPoint.y < 0) return 
          this.$emit('move', currentWindowPoint)
        }
      }
      document.addEventListener('mousemove', mousemoveHandler)

      const mouseupHandler = (e) => {
        if (this.isMousedown)  {
          this.isMousedown = false;
        }
        document.removeEventListener('mousemove', mousemoveHandler)
        document.removeEventListener('mouseup', mouseupHandler)
      }
      document.addEventListener('mouseup', mouseupHandler)

      console.log('查看n', n)
    },

    /** 获取Window对象坐标 */
    getWindowPoint(e) {
      // 计算原点位置
      const point = {
        x: e.clientX - this.absolutePoint.x, 
        y: e.clientY - this.absolutePoint.y
      }
      return point
    },
    /** 获取鼠标当前位置 */
    getMousePoint(e) {
      const point = {
        x: e.clientX,
        y: e.clientY
      }
      return point
    }
  }
}
</script>

