<template>
  <div class="dock" :class="{toUpper: init}" :style="{width: dockWidth}">
    <div class="dock-background" :style="{width: dockWidth}"></div>
    <dock-item v-for="app in apps" :key="app.name" :name="app.name" :logo="app.logo"></dock-item>
  </div>
</template>

<script>
import DockItem from './DockItem.vue'

export default {
  props: {
    apps: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      init: false
    }
  },
  mounted() {
    this.init = true
  },
  computed: {
    dockWidth() {
      return `calc(${this.apps.length * 7}vh + 60px)`
    }
  },
  components: {
    'dock-item': DockItem
  }
}
</script>

<style lang="less" scoped>
@keyframes toUpper {
  0% { transform: translate(-50%, 0%);}
  100% { transform: translate(-50%, -100%);}
}
.toUpper {
  animation: toUpper .5s ease 0s 1 normal forwards !important;
}

.dock {
  position: fixed;
  left: 50%;
  top: 100%;
  transform: translate(-50%, 0%);

  padding-left: 30px;
  width: 10vh;
  height: 3vh;
  border-radius: 2vh 2vh 0 0;

  box-sizing: border-box;

  .dock-background {
    position: absolute;

    padding-left: 30px;
    padding-right: 30px;
    height: 80%;
    background-color: aqua;
    border-radius: 6vh 6vh 1vh 1vh;

    box-sizing: border-box;
    transform: translateX(-30px);
    opacity: .3;
  }

}
</style>