<template>
  <div class="window">
    <top-bar
      ref="top-bar"
      @move="move"
    >
    </top-bar>
  </div>
</template>

<script>
import TopBar from './TopBar.vue'

export default {
  components: {
    'top-bar': TopBar
  },
  methods: {
    move(e) {  
      this.$el.style.left =  e.x + 'vh'    
      this.$el.style.top = e.y + 'vh'    
    },
  },
  mounted() {
    this.$el.style.top = '10vh'
    this.$el.style.left = '10vh'
    this.$el.style.height = '30vh'
    this.$el.style.width = '30vh'
  }
}
</script>

<style lang="less" scoped>
.window {
  position: fixed;
  width: 30vh;
  height: 30vh;
  background-color: rgb(237, 237, 245);
  border-radius: .5vh;

  box-shadow: 4px;
  box-shadow: 1vh 1vh 2vh .01vh rgba(0, 0, 0, .2);
}
</style>