<template>
  <div id="login" class="page">
    <sub-block v-for="item in new Array(20)" v-bind:key="item" :interval="15"></sub-block>
    <!-- <sub-block></sub-block> -->
    <el-card class="login-card" :class="{toBeTransparent: isLogin}" ref="login-card">
      <div slot="header" class="login-header">
        <span>Welcome</span>
      </div>
      <el-form :rules="rules" :model="loginForm" ref="loginForm">
        <el-form-item prop="username">
          <el-input placeholder="username" v-model="loginForm.username"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input placeholder="password" v-model="loginForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button class="login-submit" type="primary" :loading="loginButtonLoading" @click="login">Login</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import SubBlock from '../components/Login/SubBlock.vue'

export default {
  data() {
    return {
      /** 表单提交参数 */
      loginForm: {
        username: 'root',
        password: 'yipeidong123'
      },
      /** 表单校验规则 */
      rules: {
        username: [
          { min:3, max: 12, message: 'Username length must be 3 to 12 characters.', trigger: 'blur'},
          { validator: this.usernameValidate, trigger: 'blur'}
        ],
        password: [
          { min:3, max: 12, message: 'Password length must be 3 to 12 characters.', trigger: 'blur'}
        ]
      },
      /** 登陆定时器（用于防抖） */
      loginTimmer: null,
      /** 登陆等待状态 */
      loginButtonLoading: false,
    }
  },
  methods: {
    /** 登陆 */
    login() {
      this.$refs['loginForm'].validate((valid, err) => {
        if(valid) {
          this.loginButtonLoading = true
          clearTimeout(this.loginTimmer)
          this.loginTimmer = setTimeout(() => {
            const {username, password} = this.loginForm
            this.$api.login({ username, password})
              .then(data => {
                this.$store.commit('login')
                setTimeout(() => {
                  this.$router.push('/')
                  this.loginButtonLoading = false
                }, 1200)
              })
              .catch(err => {
                this.$message({
                  message: err.message,
                  type: 'error'
                })
                this.loginButtonLoading = false
              })
          }, 500)
          
        }
      })
    },

    /** 表单验证 */
    usernameValidate(rule, value, callback) {
      const firstLetter = typeof value?.[0] === 'string' ? value[0] : '?'
      if((firstLetter>='a'&&firstLetter<='z')||(firstLetter>='A'&&firstLetter<='Z')) 
        callback()
      else 
        callback(new Error('The first character must be a letter.'))
    },
  },
  components: {
    'sub-block': SubBlock
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin
    }
  }
}
</script>

