import Vue from 'vue'
import VueRouter from 'vue-router'
import Desktop from '../views/Desktop.vue'
import Login from '../views/Login.vue'
import store from '../store/index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Desktop',
    component: Desktop,
    meta: {
      title: 'Desktop'
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: 'Login'
    }
  }
]

const router = new VueRouter({
  routes
})

/** 导航守卫 */
router.beforeEach(async (to, from, next) => {
  /** 更换名称 */
  if (to.meta.title) document.title = to.meta.title

  /** 登陆守卫 */
  if(!store.state.isLogin && to.name !== 'Login') return next('/login')
  if(store.state.isLogin && to.name === 'Login') return next('/')

  /** 默认 */
  next()
})

export default router
